<template>
    <form @submit.prevent="submit"  class="block-form">
        <label for=""></label>
        <div class="f-cells">
            <div class="f-cell">
                <label for="name"></label>
                <div class="inline">
                    <input type="text" placeholder="" v-model.lazy="form.name" ref="name" name="name">
                </div>
            </div>

            <div class="f-cell">
                <label for="age"></label>
                <div class="inline">
                    <input type="number" placeholder="0-70" v-model.lazy="$v.form.age.$model" ref="age" name="age">
                </div>
            </div>
            <div class="f-cell">
                <label for="age"></label>
                <div class="inline">
                    <radio  name="sex"  value="1" @input="onInput" v-model="form.sex" checked></radio>
                    <radio  name="sex"  value="0" @input="onInput" v-model="form.sex" ></radio>
                </div>
            </div>
        </div>
        <label for=""></label>
        <div class="f-cells">
            <div class="f-cell">
                <label for="insuranceMoney"></label>
                <div class="inline">
                    <input type="text" placeholder="" v-model="form.insuranceMoney">
                    <span></span>
                </div>
            </div>
            <div class="f-cell">
                <label for="paymentMethod"></label>
                <div class="inline">
                    <radio  name="paymentMethod"  value="1" @input="onInput" v-model="form.paymentMethod" checked></radio>
                    <radio  name="paymentMethod"  value="12" @input="onInput" v-model="form.paymentMethod"></radio>
                </div>
            </div>
             <div class="f-cell block">
                <label for="paymentMethod"></label>
                <div class="inline">
                    <radio  name="payTimeLimit"  value="1" @input="onInput" v-model="form.payTimeLimit" ></radio>
                    <radio  name="payTimeLimit"  value="3"  @input="onInput" v-model="form.payTimeLimit">3</radio>
                    <radio  name="payTimeLimit"   value="5" @input="onInput"  v-model="form.payTimeLimit">5</radio>
                    <radio  name="payTimeLimit"   value="10" @input="onInput"  v-model="form.payTimeLimit" checked>10</radio>
                    <radio  name="payTimeLimit"   value="15" @input="onInput"  v-model="form.payTimeLimit">15</radio>
                    <radio  name="payTimeLimit"  value="20" @input="onInput"  v-model="form.payTimeLimit">20</radio>
                </div>
            </div>
            <div class="f-cell">
                <label for="insuranceMoney"></label>
                <div class="inline">
                    <input type="text" name="insuranceExpenses" v-model="insuranceExpenses" >
                    <span></span>
                </div>
            </div>
            <div class="f-cell">
                <label for="insuranceMoney"></label>
                <div class="inline">
                    <input type="text"  name="cum_Prem" v-model="cum_Prem" >
                    <span></span>
                </div>
            </div>
        </div>
        <mt-button type="danger" size="large" class="btn-submit"></mt-button>
         </form>
</template>
<script>
import { required, minLength, between } from 'vuelidate/lib/validators'

import radio from '../../common/radio';
import { MessageBox } from 'mint-ui';
export default {
    components:{
        radio
    },  
    data() {
        return {
            form:{
                name: "",
                age: "",
                insuranceMoney: 10000,
                sex:"",
                paymentMethod:'',
                payTimeLimit:''
            },
            submitStatus: null,
            insuranceExpenses:"",
            cum_Prem:"",
        }
    },
    validations: {
        form:{
            // name: {
            //     minLength: minLength(4)
            // },
            age: {
                required,
                between: between(0, 70)
            }
        }
    },
    watch:{
        "form":{   
             handler:function(val,oldval){
               if(this.$v.form.age.$error){
                    setTimeout(function(){
                        MessageBox('', '');
                    },500)
                    return;
                }else{
                    this.fetchData()
                }
            },
            deep:true
      }
    },
   computed:{
       age:function(){
           if(this.age==''){
                setTimeout(function(){
                    MessageBox('', '');
                },500)
           }
       }
   },
 
    methods:{
        onInput:function(state,key) {
          this.form[key]=state;
        },
        fetchData:function(){
            const formData = JSON.stringify(this.form);
            requestGetCalculus(formData).then((res)=>{
                if(res.status=='200'){
                    this.insuranceExpenses=res.data.insuranceExpenses;
                    this.cum_Prem=res.data.cum_Prem;
                }    
            }).catch((error)=>{
                console.log(error);
            });
        },
        submit:function(){
            const formData = JSON.stringify(this.form); // 
            if (this.$v.$invalid) {
                MessageBox('', '');
            } else {
                this.$router.push({ path: 'result', query: this.form})
            }
        }
    },
    // beforeCreate:function(){
    //     console.log('')
    // },
    // created:function(){
    //     console.log('$el');
    // },
    // beforeMount:function(){
    //     console.log('');
    // },
    // mounted:function(){
    //     console.log('$el');
    // },
    // beforeUpdate:function(){
    //      console.log('');
    // },
    // updated:function(){
    //     this.$nextTick()
    //      console.log('DOM');
    // },
    // beforeDestroy:function(){
    //      console.log('');

    // },
    // destroyed:function(){
    //      console.log('');
    // }

}


</script>
<style lang="scss" scoped>
@import  '~@/styles/util/form.scss';

</style>




