<template>
    <div>
        <div class="block-ts">

        </div>
        <div class="block-ys">
            <img src="" alt="">
            <ul>
                <li v-for="item in features" :key="item.id">
                    <img :src="item.src" alt="">
                    <div class="item-text">
                        <h2>{{item.title}}</h2>
                        <p>{{item.desc}}</p>
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
export default {
  name: "feature",
  data() {
    return {
      features: [
        {
          src: require("../../../../static/img/calculate/jiaying/feature/ts1.png"),
          title: "",
          desc: "3%"
        },
        {
          src: require("../../../../static/img/calculate/jiaying/feature/ts2.png"),
          title: "",
          desc: ""
        },
        {
          src: require("../../../../static/img/calculate/jiaying/feature/ts3.png"),
          title: "",
          desc: ""
        },
        {
          src: require("../../../../static/img/calculate/jiaying/feature/ts4.png"),
          title: "",
          desc: ""
        },
        {
          src: require("../../../../static/img/calculate/jiaying/feature/ts5.png"),
          title: "",
          desc:
            "/"
        }
      ]
    };
  }
};
</script>

<style lang="scss">
.block-ys {
  padding: 10px;
  ul {
    padding-left: 0;
    margin: 0;
    li {
      list-style: none;
      display: flex;
      align-items: flex-start;
      margin-bottom: 20px;

      .item-text {
        color: #b4844e;
        flex: 1;
        h2 {
          font-size: 18px;
          margin: 0;
        }
        p {
          margin: 5px 0 0 0;
           font-size: 14px;
        }
      }
      img {
        width: 70px;
        margin-right: 10px;
      }
    }
  }
}
</style>




